@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body *{font-family:'Roboto', sans-serif;box-sizing:border-box;color:#444;font-size:14px}.friend-list-main{display:grid;align-items:start;justify-content:center;padding:100px 10px;height:100vh;width:100vw;overflow-y:auto;background:linear-gradient(100deg, #200c32, #e74d89, #febaa6);background-size:180% 180%;animation:gradient-animation 9s ease infinite}.friend-list-main .friend-list-box{width:400px;border-radius:5px;background-color:#fff;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23)}@media only screen and (max-width: 767px){.friend-list-main .friend-list-box{width:calc(100vw - 30px)}}.friend-list-main .friend-list-box>h3{background-color:#367EF5;padding:10px;font-weight:bold;color:#fff;text-align:center;font-size:18px;border-radius:5px 5px 0px 0px}.friend-list-main .friend-list-box .friend-list-box-inner{padding:0px 10px 10px}.friend-list-main .friend-list-box .friend-list-box-inner #addfriend{box-shadow:none;outline:none;display:block;width:100%;border:1px solid #ddd;padding:8px 10px;border-radius:5px;margin-top:10px;background-color:#e6efff;color:#333}.friend-list-main .friend-list-box .friend-list-box-inner #searchbox{box-shadow:none;outline:none;display:block;width:100%;border:0;border-bottom:1px solid #ddd;padding:8px 10px;margin-top:5px;color:#333}.friend-list-main .friend-list-box .sortbtn{margin-top:15px;text-align:right}.friend-list-main .friend-list-box .sortbtn button{background-color:#fff;border:0;box-shadow:none;cursor:pointer;color:#444;font-size:12px;font-weight:bold}.friend-list-main .friend-list-box .sortbtn button.active{color:#367EF5}.friend-list-main .friend-list-box .friend-list-ul li{padding:10px;margin-top:5px;border:1px solid #ddd;border-radius:5px;display:grid;grid-template-columns:auto 20px 20px;grid-column-gap:10px}.friend-list-main .friend-list-box .friend-list-ul li>span{display:grid;align-items:center;justify-content:center}.friend-list-main .friend-list-box .friend-list-ul li>span i{font-size:17px;color:#ccc;transition:color 0.2s;cursor:pointer}.friend-list-main .friend-list-box .friend-list-ul li>span i.fa-star{color:#367EF5;transition:color 0.3s}.friend-list-main .friend-list-box .friend-list-ul li>span i:hover{color:#367EF5;transition:color 0.3s}.friend-list-main .friend-list-box .friend-list-ul li>span:first-child{text-align:left;justify-content:start;cursor:default}.friend-list-main .friend-list-box .friend-list-ul li>span:first-child>span:first-child{color:#444;font-size:16px;font-weight:bold;margin-bottom:5px}.friend-list-main .friend-list-box .friend-list-ul li>span:first-child>span:nth-child(2){color:#A9A9A9;font-size:10px}.friend-list-main .pagination{padding:5px 10px 10px}.friend-list-main .pagination ul{text-align:center}.friend-list-main .pagination ul li{display:inline-block;border:1px solid #367EF5;width:25px;height:25px;line-height:25px;font-size:12px;color:#367EF5;border-radius:50%;margin-right:5px;margin-bottom:5px;cursor:pointer}.friend-list-main .pagination ul li.active{background-color:#367EF5;border:1px solid #367EF5;color:#fff;cursor:default}.friend-list-main .pagination ul li:hover{background-color:#367EF5;border:1px solid #367EF5;color:#fff;transition:0.3s}@keyframes gradient-animation{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}.error{margin-top:5px;color:#FF4C4C;font-size:12px;text-align:center}.successalert{margin-top:5px;color:green;font-size:12px;text-align:center}.modalwrapper{position:absolute;left:0;top:0;height:100%;width:100%;z-index:1500;background-color:rgba(0,0,0,0.3)}.modalwrapper .modalmain{width:300px;height:auto;position:absolute;left:0;right:0;top:40%;transform:translateY(-50%);margin:0 auto;background-color:#fff;padding:20px;border-radius:5px}.modalwrapper .modalmain p{font-weight:bold;text-align:center;line-height:1.4;max-width:250px;margin:0 auto}.modalwrapper .modalmain p strong{color:#367EF5}.modalwrapper .modalmain .modalbtns{margin-top:15px;text-align:center}.modalwrapper .modalmain .modalbtns button{color:#fff;border:0;box-shadow:none;outline:none;padding:0 15px;border-radius:3px;display:inline-block;font-size:12px;line-height:23px;cursor:pointer}.modalwrapper .modalmain .modalbtns button.yes{border:1px solid rgba(54,126,245,0.9);background-color:rgba(54,126,245,0.9);margin-right:10px}.modalwrapper .modalmain .modalbtns button.yes:hover{background-color:#367ef5;transition:background-color 0.3s}.modalwrapper .modalmain .modalbtns button.no{border:1px solid rgba(119,119,119,0.9);background-color:rgba(119,119,119,0.9)}.modalwrapper .modalmain .modalbtns button.no:hover{background-color:#777;transition:background-color 0.3s}

